import React, {useState, useEffect} from 'react';
import {Route, Routes, Link, Navigate} from 'react-router-dom';
import Today from './pages/Today';
import Day from './pages/Day';
import moment from 'moment';
import axios from 'axios';
import {stringify} from 'query-string';
import {Select} from 'antd';

const {Option} = Select;

const App = () => {
  const [weather, setWeather] = useState();
  const [lat, setLat] = useState();
  const [lon, setLon] = useState();
  const [weathers, setWeathers] = useState();
  const today = moment().format('YYYY-MM-DD');
  const [todayForecast, setTodayForecast] = useState();
  const [location, setLocation] = useState();

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position) => {
      setLat(position.coords.latitude);
      setLon(position.coords.longitude);
    });
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      const params = {
        lat,
        lon,
        units: 'metric',
        appid: process.env.REACT_APP_API_KEY,
      };

      await axios.get(
          `${process.env.REACT_APP_API_URL}/weather?${stringify(params)}`,
      )
          .then((response) => setWeather(response.data));

      await axios.get(
          `${process.env.REACT_APP_API_URL}/forecast?${stringify(params)}`,
      )
          .then((response) => setWeathers(response.data));
    };

    if (lat !== undefined && lon !== undefined) {
      fetchData();
    }
  }, [lat, lon]);

  useEffect(() => {
    if (weathers !== undefined) {
      const Morning = weathers?.list.filter((weather) => {
        return (
          weather.dt >= Math.floor(new Date(today + ' 06:00') / 1000) &&
          weather.dt <= Math.floor(new Date(today + ' 12:00') / 1000)
        );
      });

      const Afternoon = weathers?.list.filter((weather) => {
        return (
          weather.dt >= Math.floor(new Date(today + ' 12:00') / 1000) &&
          weather.dt <= Math.floor(new Date(today + ' 16:59') / 1000)
        );
      });

      const Evening = weathers?.list.filter((weather) => {
        return (
          weather.dt >= Math.floor(new Date(today + ' 17:00') / 1000) &&
          weather.dt <= Math.floor(new Date(today + ' 21:00') / 1000)
        );
      });

      const Night = weathers?.list.filter((weather) => {
        return (
          weather.dt >= Math.floor(new Date(today + ' 21:00') / 1000) &&
          weather.dt <= Math.floor(new Date(today + ' 23:59') / 1000)
        );
      });

      setTodayForecast({
        Morning,
        Afternoon,
        Evening,
        Night,
      });
    }
  }, [weathers]);

  const props = {weather, todayForecast, weathers};

  const onSearch = async (value) => {
    const response = await axios.get(`http://geodb-free-service.wirefreethought.com/v1/geo/cities?namePrefix=${value}&hateoasMode=false&limit=5`);
    setLocation(response.data.data);
  };

  /* eslint-disable max-len */
  return (
    <>
      <nav className="bg-white border-gray-200 px-2 sm:px-4 py-2.5 rounded dark:bg-gray-900">
        <div className="container flex flex-wrap justify-between items-center mx-auto">
          <a href="https://flowbite.com/" className="flex items-center">
            <span className="self-center text-xl font-semibold whitespace-nowrap dark:text-white">Weather Forecast</span>
          </a>
          <div className="flex md:order-2">
            <button type="button" data-collapse-toggle="navbar-search" aria-controls="navbar-search" aria-expanded="false" className="md:hidden text-gray-500 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none focus:ring-4 focus:ring-gray-200 dark:focus:ring-gray-700 rounded-lg text-sm p-2.5 mr-1">
              <svg className="w-5 h-5" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd"></path></svg>
              <span className="sr-only">Search</span>
            </button>
            <div className="relative w-64">
              <div className="flex absolute inset-y-0 left-0 items-center pl-3 pointer-events-none">
                <svg aria-hidden="true" className="w-5 h-5 text-gray-500 dark:text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
              </div>
              <Select
                showSearch
                placeholder="Search location"
                optionFilterProp="children"
                onChange={(value) => {
                  const location = value.split('&');
                  setLat(location[0]);
                  setLon(location[1]);
                }}
                onSearch={onSearch}
                // filterOption={(input, option) => option.children.toLowerCase().includes(input.toLowerCase())}
                id="default-search"
                className="block p-4 pl-10 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
              >
                {location?.map((loc, key) => {
                  const {latitude, longitude, city, region, country} = loc;
                  return (<Option value={`${latitude}&${longitude}`} key={key}>{city}, {region}, {country}</Option>);
                })}
              </Select>
            </div>
            <button data-collapse-toggle="navbar-search" type="button" className="inline-flex items-center p-2 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600" aria-controls="navbar-search" aria-expanded="false">
              <span className="sr-only">Open menu</span>
              <svg className="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd"></path></svg>
            </button>
          </div>
          <div className="hidden justify-between items-center w-full md:flex md:w-auto md:order-1" id="navbar-search">
            <div className="relative mt-3 md:hidden">
              <div className="flex absolute inset-y-0 left-0 items-center pl-3 pointer-events-none">
                <svg className="w-5 h-5 text-gray-500" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd"></path></svg>
              </div>
              <input type="text" id="search-navbar" className="block p-2 pl-10 w-full text-gray-900 bg-gray-50 rounded-lg border border-gray-300 sm:text-sm focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Search..." />
            </div>
            <ul className="flex flex-col p-4 mt-4 bg-gray-50 rounded-lg border border-gray-100 md:flex-row md:space-x-8 md:mt-0 md:text-sm md:font-medium md:border-0 md:bg-white dark:bg-gray-800 md:dark:bg-gray-900 dark:border-gray-700">
              <li>
                <Link to="today" className="block py-2 pr-4 pl-3 text-white bg-blue-700 rounded md:bg-transparent md:text-blue-700 md:p-0 md:dark:text-white dark:bg-blue-600 md:dark:bg-transparent">Today</Link>
              </li>
              <li>
                <Link to="day" className="block py-2 pr-4 pl-3 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 dark:text-gray-400 md:dark:hover:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent">5 Day</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <Routes>
        <Route path="/" element={<Today {...props} />} />
        <Route path="/today" element={<Today {...props} />} />
        <Route path="/day" element={<Day {...props} />} />
        <Route path="*" element={<Navigate to ="/" />}/>
      </Routes>
    </>
  );
};

export default App;
